html, body {
  --blue-color: #00acdc;
  margin: 0;
}

body {
  font-family: Roboto, Times, serif;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.logo__container {
  align-items: center;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__tools {
  box-sizing: content-box;
  margin: 0;
  padding: 0;
}

.nav__list {
  box-sizing: content-box;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__li {
  text-align: center;
  align-items: center;
  margin: 0 10px;
  list-style-type: none;
  display: flex;
}

.nav__li:first-child {
  margin-left: 0;
}

.nav__li:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

a.blue-link {
  color: var(--blue-color);
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  box-sizing: content-box;
  border: 2px solid var(--blue-color);
  color: #000;
  border-radius: 8px;
  width: 33px;
  height: 0;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--blue-color);
}

